:root {
  --text-color: #00acdc;
}

* {
  margin: 0;
}

body {
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

.header {
  background: #fff;
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.logo__img {
  margin: 10px 0;
  display: flex;
}

.nav .nav__list {
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  position: relative;
}

.nav__item:not(:last-child) {
  margin: 0 20px 0 0;
}

.nav__link {
  color: #000;
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
}

.is-active, .nav__link:hover {
  color: var(--text-color);
}

.nav .nav__list .nav__item:after {
  content: " ";
  border-radius: 8px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 4px;
  display: flex;
  position: absolute;
  bottom: 0;
  left: 0;
}

.nav .nav__list .nav__item:first-child:after {
  background-color: var(--text-color);
}

